import React,{Component} from 'react';


const locurl = "http://localhost:8900/location"
const resturl= "http://localhost:8900/restaurentshome?city=";


        class Search extends Component {
            constructor(){
            super()
            this.state={
                location:'',
                city:'',
                restaurents:''

            }
        }


        handleCity=(event) => {
            this.setState({restaurents:''})
            this.setState({city:event.target.value})
            var cityid = parseInt(event.target.value)
            sessionStorage.setItem('cityid',event.target.value)
            console.log(`${resturl}${cityid}`)
            fetch(`${resturl}${cityid}`,{method:'GET'})
            .then((res) => res.json())
            .then((data) => {
                this.setState({restaurents:data})
            })
        }


        renderCity = (data) => {
            if(data){
                return data.map((item) => {
                    return(
                        <option value = {item.city}>
                          {item.name}  

                        </option>
                    )
                })

            }

        }

        renderRest= (data) => {
            if(data){
                return data.map((item) => {
                    return(
                        <option value={item._id}>
                          {item.name} | {item.location} 

                        </option>
                    )
                })

            }

        }


        render(){
            return(
                
            

                <div class="imageContainer">
            <div id="logo">
                <b>r!</b>
            </div>
            <div class="heading">
                
            <a>Cheaf'$ Cooking</a>

               <p> Find the best restaurents, cafes, bars</p>
               &nbsp;
               <p><strong>Feel Heaven by saying "yum yum in your tum tum"<span>&hearts;</span></strong></p>
            </div>
            
                    <center>
                        <select onChange = {this.handleCity}>
                        {this.renderCity(this.state.location)}
                        </select>
                    <input list ="restaurent" name="rests" id="rests"/>
                        <datalist id = "restaurent">
                            {this.renderRest(this.state.restaurents)}
                        </datalist>
                    </center>
                &nbsp;

                    </div>
                
            
            )
        }
        componentDidMount() {
            fetch(locurl,{method:'GET'})
            .then((res) => res.json())
            .then((data) => {
             this.setState({location:data})

            })

        }
    
    }



export default Search;