import React, {Component} from 'react';
import axios from 'axios';
import {Link} from "react-router-dom";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';


const restdetail="http://localhost:8900/restaurentsdetails"; 
class Restaurent extends Component{
    constructor(props){
        super(props)
        this.state={
            rest:''
        }
    }


    backbutton=(event) =>{
        let mealid = parseInt(sessionStorage.getItem('mealid'))
        this.props.history.push(`/details/${mealid}`)
    }

    
    render(){
        console.log(">>>>",this.state.rest)
        var {rest} = this.state
        return(
            <div className="container">
                <div className="panel panel-primary">
                    <div className="panel-heading">
                        <h2>{rest.name}</h2>
                    </div>
                    </div>

        
                    <div className="panel-body">
                        <div className="row">
                            <div className="col-md-6">
                                <img className="img-responsive" src={rest.thumb}/>
                            </div>
                            
                        </div> 
                    
                        <br/>
                        <div className="row">
                            <button class="btn btn-danger btn-lg"
                            onClick={this.backbutton}>Back</button>
                            &nbsp;
                            <Link to={`/order/${this.props.match.params.id}`} class="btn btn-success btn-lg">Place Order</Link>
                        </div>
                        </div>

              <div>          
          <Tabs>
            <TabList>
              <Tab>OverView</Tab>
              <Tab>Contact</Tab>
            </TabList>
            <TabPanel><h3>About this Place</h3>
            <h4>Cuisine</h4>
            <p> North-Indian,Fast-food,Italian,South-Indian</p>
            <h4>Average Cost</h4>
            <p>â‚¹1000 for two people (approx.)</p>

            </TabPanel>
            <TabPanel>
            <h4>Phone Number</h4>
            <p>9876354798</p>
            <h2>Olive Garden</h2>
            <p>Road-no=4,RedLane,London</p>
            </TabPanel>
          </Tabs>
        </div>,
          </div>          

                    
        )
    }               
                                    

    componentDidMount(){
        let restid = parseInt(this.props.match.params.id)
        axios.get(`${restdetail}/${restid}`)
        .then((response) => {this.setState({rest:response.data[0]})})
    }}
    


        
export default Restaurent;