import React,{Component} from 'react';
import axios from 'axios';
import OrderView from './orderView';
import './orderDisplay.css';




const orderdetail="http://localhost:8900/allOrder";

class OrderDisplay extends Component{
    constructor(){
        super()

        this.state={
            orders:''
        }
    }
    render(){
        return(
            <div>
                <div className="container">
                    <div>
                        <OrderView orderdata={this.state.orders}/>
                    </div>
                </div>
                <div className="Messenger">
                    <div className="messengeSender__top">
                    <input type="text"
                    placeholder={`Drop your Review`}/>
                </div>
                </div>
                &nbsp;&nbsp;

                <div className="Messengertwo">
                    <div className="messengeSender__bottom">
                    <input type="text"
                    placeholder={`Rate our experience`}/>
                </div>
                </div>
                &nbsp;

            
                
                <span><u><strong>Comment</strong></u></span>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <u><h4><a>Track your order by giving your order number</a></h4></u>
                
                <span><strong>Contact us if any queries (040-987654323)</strong></span>
                

            </div>
            
    
        )
    }






componentDidMount(){
   
   
    axios.get(`${orderdetail}`)
    .then((response) => {this.setState({orders:response.data})})
}
}

export default OrderDisplay;