import React,{Component} from 'react';
import axios from 'axios';
import DetailkaDisplay from './DetailDisplay';
import CostFilter from '../filter/costFilter';
import CusineFilter from '../filter/cuisineFilter';
import Pagination from "react-js-pagination";
import'./details.css';

const restdetail = "http://localhost:8900/restaurentslist";
class Details extends Component {
    constructor(props){
        super(props)

        this.state={
            cityId: parseInt(sessionStorage.getItem('cityId'))? parseInt(sessionStorage.getItem('cityId')):1,
            restlist:[],
            activePage:5
        }
    }

    setDataPerCuisine(sortedData){
        this.setState({restlist:sortedData})
    }

    setDataPerCost(sortedData){
        this.setState({restlist:sortedData})
    }

    handlePageChange(pageNumber) {
        console.log(`active page is ${pageNumber}`);
        this.setState({activePage: pageNumber});
      }
    render(){
        return(
            <div>
            <div>
                <div className="row">
                    <div className="col-md-2">
                        <CusineFilter mealIdNumber={this.props.match.params.id} cityIdNo={this.state.cityId} restpercuisine={(data) => {this.setDataPerCuisine(data)}}/>
                        <CostFilter restpercost={(data) => {this.setDataPerCost(data)}} mealIdNumber={this.props.match.params.id} cityIdNo={this.state.cityId}/>
                    </div>
                    <div className="col-md-10">
                        <DetailkaDisplay restdata={this.state.restlist}/> 
                    </div>
                    
                </div>
                
            </div>
            <center>
          <div class="paginate">
            <Pagination
          activePage={this.state.activePage}
          itemsCountPerPage={2}
          totalItemsCount={6}
          pageRangeDisplayed={5}
          onChange={this.handlePageChange.bind(this)}
        />
        
      </div>
    
    
           
        </center>
            </div>
            
        
            
        )
    }

    componentDidMount(){
        sessionStorage.setItem('mealid',this.props.match.params.id)
        let mealid = parseInt(this.props.match.params.id)
        axios.get(`${restdetail}/${this.state.cityId}/${mealid}`)
        .then((response) => {this.setState({restlist:response.data})})
    }
}


export default Details;
